		document.querySelector("#" + str + " button").innerHTML = document.querySelector("#" + str + " button").innerHTML.split("▼")[0] + " ▲";
	} else {
		document.querySelector("#" + str + " button").innerHTML = document.querySelector("#" + str + " button").innerHTML.split("▲")[0] + "▼";
	}
	for (let i = 0; i < arr.length; i++) {
		if (arr[i].style.display != "none") {
